<template>
  <v-app>
      <v-content>
        <v-container>
          <v-snackbar
            v-model="installDialog"
            vertical
            color="white"
            :timeout="installDialogTimeout"
          >
            <p>
              Would you want install the app?
            </p>
            <br />
            <v-btn
              color="green"
              dark
              block
              rounded
              @click="installApp()"
            >
              Yes, Install It!
            </v-btn>
            <v-btn
              color="red"
              dark
              text
              block
              @click="disMissInstall()"
            >
              No
            </v-btn>
          </v-snackbar>
          <v-row
            justify="center"
            content="center"
          >
            <v-col cols="12" md="4">
              <v-card
                class="px-3 py-3"
              >
               <v-card-title>
                 TODO You're Works
               </v-card-title>
                <v-row
                  justify="center"
                  content="center"
                >
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field
                            label="What is you're work title?"
                            placeholder="write you're title here..."
                            outlined
                            v-model="todo.title"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field
                            label="What To Do?"
                            placeholder="write you're content here..."
                            outlined
                            v-model="todo.content"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-btn
                      color="green"
                      @click="saveToDo()"
                      block
                      dark
                    >
                      SAVE TODO
                    </v-btn>
                  </v-col>
                </v-row>

                <div v-if="todos.length">
                  <hr>
                  <v-list
                          flat
                          subheader
                          three-line
                  >
                    <v-subheader>TODO LIST</v-subheader>

                    <v-list-item-group
                            v-model="todos"
                            multiple
                            active-class=""
                    >
                      <div v-for="(todo, index) in todos" :key="index">
                        <v-list-item>
                          <template v-slot:default="{ active }">
                            <v-list-item-action>
                              <v-checkbox v-model="todo.active"></v-checkbox>
                            </v-list-item-action>

                            <v-list-item-content>
                              <v-list-item-title>
                                {{todo.title}}
                              </v-list-item-title>
                              <v-list-item-subtitle>
                                {{todo.content}}
                              </v-list-item-subtitle>
                            </v-list-item-content>
                          </template>
                        </v-list-item>
                      </div>
                    </v-list-item-group>
                  </v-list>
                  <v-btn
                          color="red"
                          dark
                          block
                          @click="removeTodo()"
                  >
                    Done This Works
                  </v-btn>
                </div>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  components: {

  },

  data: () => ({
    todo: {
      title: '',
      content: '',
      active: false
    },
    todos: [],
    installDialog: false,
    installDialogTimeout: 10000,
    deferredPrompt: null
  }),
  watch:{
    todos:{
      handler(){
        localStorage.setItem('todos', JSON.stringify(this.todos))
      }
    }
  },
  methods:{
    saveToDo(){
      let emptyTodo =  {
        title: '',
        content: '',
        active: false
      };
      if (this.todo.title.length > 0){
        this.todos.push(this.todo);
      }

      this.todo = emptyTodo;
    },
    removeTodo(){
      this.todos.forEach((todo, index) => {
        if (todo.active === true){
          this.todos.splice(index)
        }
      })
    },
    installApp(){
      this.installDialog = false;
      this.deferredPrompt.prompt();
      this.deferredPrompt.userChoisce.then(choiceResult => {
        if (choiceResult.outcome === 'accepted'){
          this.installDialog = false
        }else{
          this.installDialog = false
        }
        this.installDialog = false
      });
    },
    disMissInstall(){
      this.installDialog = false
    }
  },
  mounted() {
    window.addEventListener('beforeinstallprompt', (e) => {
      e.preventDefault();
      this.deferredPrompt = e;
      if(this.isMobileCheck){
        this.installDialog = true;
      }
    });
    if (localStorage.getItem('todos')){
      this.todos = JSON.parse(localStorage.getItem('todos'))
    }
  }

};
</script>
